
import { wsk } from "@/libs/api.request.js";
import {
  URL
} from "../libs/api.request"
import Centrifuge from "centrifuge";
let socketIns = null;
export default {
  /**
   * 
   * @param {socket} api 
   * @param {socket} params 
   * @param {} cb 
   * @param {} key 
   */
  getWebSocket(api, params, cb, obj) {
    if ("WebSocket" in window && !self.connectStatus) {
      api(params).then(res => {
        const sign = res.data.access_token; //  secretusertimestamphashmapsha256
        const timestamp = res.data.timestamp; // 
        const user = res.data.user; // id
        if (user) return
        socketIns = new Centrifuge({
          url: wsk,
          user: user,
          timestamp: timestamp,
          token: sign,
          //
          refreshEndpoint: `${URL}/cperm/cent/refresh`,
        });
        // Websocket,
        // socketIns.connect();
        // msg:userid 
        socketIns.subscribe(
          // `msg:${self.$store.state.user.userID}`,
          `${obj.key}:${obj.val}`,
          message => {
            console.log(message);
            cb();
          }
        );

        socketIns.on("connect", context => {
          console.log('');
          cb();
          // console.log("", context);
        });

        socketIns.on("disconnect", context => {
          console.log('');
          // console.log("", context);
        });

        socketIns.on("error", error => {
          console.log(error);

          console.log('');
          // console.log("", error);
        });

        // websocketserver
        // window.onbeforeunload = () => {
        //   socketIns.disconnect();
        // };
      });
      return socketIns
    }
    return socketIns
  },
  closeWs() {
    if (socketIns) {
      socketIns.disconnect();
    }
  }
}
